import React, { useEffect, useState } from "react";

import ImageUploader from "../imageUploader/ImageUploader";
import { gaussianBlur } from "./utils";

import "./GaussianBlur.css";
import defaultImage from "./image-asset.jpeg";

function GaussianBlur() {
  const [image, setImage] = useState<File | null>(null);
  const [radius, setRadius] = useState<number>(3);

  useEffect(() => {
    const canvas = document.getElementById("result-image") as HTMLCanvasElement;
    const context = canvas.getContext("2d");

    const originalImage = new Image();
    originalImage.src = image
      ? window.URL.createObjectURL(image)
      : defaultImage;

    originalImage.onload = () => {
      let imageRatio = originalImage.width / originalImage.height;
      let imageWidth = canvas.width;
      let imageHeight = imageWidth / imageRatio;
      if (imageHeight > canvas.height) {
        imageHeight = canvas.height;
        imageWidth = imageHeight * imageRatio;
      }
      context?.clearRect(0, 0, canvas.width, canvas.height);
      context?.drawImage(originalImage, 0, 0, imageWidth, imageHeight);
      const imgData = context?.getImageData(0, 0, imageWidth, imageHeight);

      if (!imgData) {
        return;
      }

      context?.putImageData(
        gaussianBlur(
          imgData,
          radius,
          Math.round(imageWidth),
          Math.round(imageHeight)
        ),
        0,
        0
      );
    };
  }, [image, radius]);

  return (
    <>
      <ImageUploader setImage={setImage} />
      <div className="sigma-slider">
        <p>Izvlieties rdiusu:</p>
        <input
          type="number"
          value={radius}
          onChange={(event) => setRadius(parseInt(event.target.value, 10))}
        />
      </div>
      <div className="sigma-value">Rdiuss = {radius}</div>
      <div className="row">
        <canvas id="result-image" width={300} height={300} />
      </div>
    </>
  );
}

export default GaussianBlur;
