import React, { useEffect, useState } from "react";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
} from "chart.js";
import { Bar } from "react-chartjs-2";

import ImageUploader from "../imageUploader/ImageUploader";
import {
  equalizeColorImage,
  equalizeGrayImage,
  transformImageToGrayscale,
} from "./utils";

import "./HistogramSmoothing.css";
import defaultImage from "./image-asset.jpeg";

ChartJS.register(CategoryScale, LinearScale, BarElement);

function HistogramSmoothing() {
  const [image, setImage] = useState<File | null>(null);
  const [histogram, setHistogram] = useState<number[]>([]);
  const [equalizedHist, setEqualizedHist] = useState<number[]>([]);

  useEffect(() => {
    const originalCanvas = document.getElementById(
      "original-image"
    ) as HTMLCanvasElement;
    const originalCtx = originalCanvas.getContext("2d");

    const originalImage = new Image();
    originalImage.src = image
      ? window.URL.createObjectURL(image)
      : defaultImage;

    originalImage.onload = () => {
      /* Draw original image */
      let imageRatio = originalImage.width / originalImage.height;
      let imageWidth = originalCanvas.width;
      let imageHeight = imageWidth / imageRatio;
      if (imageHeight > originalCanvas.height) {
        imageHeight = originalCanvas.height;
        imageWidth = imageHeight * imageRatio;
      }
      originalCtx?.clearRect(0, 0, originalCanvas.width, originalCanvas.height);
      originalCtx?.drawImage(originalImage, 0, 0, imageWidth, imageHeight);
      const imgData = originalCtx?.getImageData(0, 0, imageWidth, imageHeight);
      if (!imgData) {
        return;
      }

      /* Draw grayscale version of original image */
      const resultCanvas = document.getElementById(
        "grayscale"
      ) as HTMLCanvasElement;
      const grayCtx = resultCanvas.getContext("2d");
      grayCtx?.clearRect(0, 0, resultCanvas.width, resultCanvas.height);
      const { imageData: grayscaleImg, histogram: hist } =
        transformImageToGrayscale(imgData);
      grayCtx?.putImageData(grayscaleImg, 0, 0);
      setHistogram(hist);

      /* Draw equalized gray image and get histogram */
      const { histogram: eqHistogram, imageData: equalizedImageData } =
        equalizeGrayImage(grayscaleImg, hist, imageWidth, imageHeight);
      setEqualizedHist(eqHistogram);
      const resultCanvas2 = document.getElementById(
        "grayscale-equalized"
      ) as HTMLCanvasElement;
      const eqCtx = resultCanvas2.getContext("2d");
      eqCtx?.clearRect(0, 0, resultCanvas2.width, resultCanvas2.height);
      eqCtx?.putImageData(equalizedImageData, 0, 0);

      /* Draw equalized color image and get histogram */
      const colorEqualizedCanvas = document.getElementById(
        "original-image-equalized"
      ) as HTMLCanvasElement;
      const colEqCtx = colorEqualizedCanvas.getContext("2d");
      colEqCtx?.clearRect(
        0,
        0,
        colorEqualizedCanvas.width,
        colorEqualizedCanvas.height
      );
      colEqCtx?.drawImage(originalImage, 0, 0, imageWidth, imageHeight);
      const colImgData = colEqCtx?.getImageData(0, 0, imageWidth, imageHeight);
      if (!colImgData) {
        return;
      }
      const resultImg = equalizeColorImage(colImgData, imageWidth, imageHeight);
      colEqCtx?.putImageData(resultImg, 0, 0);
    };
  }, [image]);

  const options = {
    responsive: true,
    borderWidth: 0,
    scales: {
      x: {
        display: false,
      },
      y: {
        display: false,
      },
    },
  };

  const data = {
    labels: Array.from(Array(256).keys()),
    datasets: [
      {
        label: "Count",
        data: histogram,
        backgroundColor: "rgba(0, 0, 0, 1)",
        barPercentage: 1.0,
        categoryPercentage: 1.0,
      },
    ],
  };

  const data2 = {
    labels: Array.from(Array(256).keys()),
    datasets: [
      {
        label: "Count",
        data: equalizedHist,
        backgroundColor: "rgba(0, 0, 0, 1)",
        barPercentage: 1.0,
        categoryPercentage: 1.0,
      },
    ],
  };

  return (
    <>
      <ImageUploader setImage={setImage} />
      <>
        <h3>Melnbalta attla apstrde:</h3>
        <div className="row">
          <div className="column">
            <p>Pirms:</p>
            <canvas id="grayscale" width={300} height={200} />
            <div style={{ width: 200, height: 100 }}>
              {histogram.length > 0 && <Bar options={options} data={data} />}
            </div>
          </div>
          <div className="column">
            <p>Pc:</p>
            <canvas id="grayscale-equalized" width={300} height={200} />
            <div style={{ width: 200, height: 100 }}>
              {equalizedHist.length > 0 && (
                <Bar options={options} data={data2} />
              )}
            </div>
          </div>
        </div>

        <h3>Krsaina attla apstrde:</h3>
        <div className="row">
          <div style={{ marginRight: 40 }}>
            <p>Pirms:</p>
            <canvas id="original-image" width={300} height={200} />
          </div>
          <div>
            <p>Pc:</p>
            <canvas id="original-image-equalized" width={300} height={200} />
          </div>
        </div>
      </>
    </>
  );
}

export default HistogramSmoothing;
