export function gaussianBlur(
  imageData: ImageData,
  radius: number,
  imageWidth: number,
  imageHeight: number
): ImageData {
  const data = imageData.data;

  /* Create temp data array for new image */
  const tmpData = new Uint8ClampedArray(data.length);
  for (let i = 0; i < data.length; i++) {
    tmpData[i] = data[i];
  }

  /* Create kernel */
  const sigma = Math.max(radius / 2, 1);
  const kernelWidth = 2 * radius + 1;
  const kernel = new Array(kernelWidth);

  for (let i = 0; i < kernelWidth; i++) {
    kernel[i] = new Array(kernelWidth).fill(0);
  }
  let sum = 0;

  for (let i = -radius; i <= radius; i++) {
    for (let j = -radius; j <= radius; j++) {
      /* Gaussian function */
      const numerator = -(Math.pow(i, 2) + Math.pow(j, 2));
      const denominator = 2 * Math.pow(sigma, 2);
      const value =
        Math.pow(Math.E, numerator / denominator) /
        (2 * Math.PI * Math.pow(sigma, 2));
      kernel[i + radius][j + radius] = value;
      sum += value;
    }
  }

  /* Normalize kernel, so all values add up to 1 */
  for (let i = 0; i < kernelWidth; i++) {
    for (let j = 0; j < kernelWidth; j++) {
      kernel[i][j] /= sum;
    }
  }

  /* Helper function to access pixel data with (x, y) */
  const getPixelValues = (x: number, y: number) => {
    return {
      red: data[y * imageWidth * 4 + x * 4],
      green: data[y * imageWidth * 4 + x * 4 + 1],
      blue: data[y * imageWidth * 4 + x * 4 + 2],
    };
  };

  /* Apply kernel to pixel values */
  for (let y = 0; y < imageHeight; y++) {
    for (let x = 0; x < imageWidth; x++) {
      let redValue = 0;
      let greenValue = 0;
      let blueValue = 0;

      for (let kernelX = -radius; kernelX <= radius; kernelX++) {
        for (let kernelY = -radius; kernelY <= radius; kernelY++) {
          let kernelValue = kernel[kernelX + radius][kernelY + radius];
          if (
            x - kernelX < 0 ||
            x - kernelX >= imageWidth ||
            y - kernelY < 0 ||
            y - kernelY >= imageHeight
          ) {
            continue;
          }
          redValue +=
            getPixelValues(x - kernelX, y - kernelY).red * kernelValue;
          greenValue +=
            getPixelValues(x - kernelX, y - kernelY).green * kernelValue;
          blueValue +=
            getPixelValues(x - kernelX, y - kernelY).blue * kernelValue;
        }
      }

      tmpData[y * imageWidth * 4 + x * 4] = redValue;
      tmpData[y * imageWidth * 4 + x * 4 + 1] = greenValue;
      tmpData[y * imageWidth * 4 + x * 4 + 2] = blueValue;
    }
  }

  return new ImageData(tmpData, imageWidth, imageHeight);
}
