/* Transform image to grayscale using rec601 luma url: https://en.wikipedia.org/wiki/Grayscale */
export function transformImageToGrayscale(imageData: ImageData): {
  imageData: ImageData;
  histogram: number[];
} {
  const data = imageData.data;

  const histogram = new Array(256).fill(0);

  for (let i = 0; i < data.length; i += 4) {
    const R = data[i];
    const G = data[i + 1];
    const B = data[i + 2];
    const Lum = Math.floor(0.299 * R + 0.587 * G + 0.114 * B);
    histogram[Lum]++;
    data[i] = Lum;
    data[i + 1] = Lum;
    data[i + 2] = Lum;
  }

  return { imageData, histogram };
}

/* Do histogram equalization for gray image */
export function equalizeGrayImage(
  imageData: ImageData,
  histogram: number[],
  width: number,
  height: number
): {
  imageData: ImageData;
  histogram: number[];
} {
  const data = imageData.data;

  /* Get cumulative distribution function and cdfMin for function */
  const cumulative = new Array(256).fill(0);
  let cdfMin = 0;
  let sum = 0;

  for (let i = 0; i < 256; i++) {
    if (histogram[i] !== 0 && cdfMin === 0) {
      cdfMin = histogram[i];
    }
    sum += histogram[i];
    cumulative[i] = sum;
  }

  /* Do equalization */
  const equalized = new Array(256).fill(0);
  for (let i = 0; i < 256; i++) {
    /* h(v) function from https://en.wikipedia.org/wiki/Histogram_equalization */
    const hV = Math.round(
      ((cumulative[i] - cdfMin) * 255) / (width * height - cdfMin)
    );
    equalized[i] = hV;
  }

  /* Get new histogram */
  const equalizedHistogram = new Array(256).fill(0);
  for (let i = 0; i < data.length; i += 4) {
    const x = data[i];
    equalizedHistogram[equalized[x]]++;
    data[i] = equalized[x];
    data[i + 1] = equalized[x];
    data[i + 2] = equalized[x];
  }

  return { imageData, histogram: equalizedHistogram };
}

/* Do histogram equalization for color image */
export function equalizeColorImage(
  imageData: ImageData,
  width: number,
  height: number
): ImageData {
  const data = imageData.data;

  /* Get histograms */
  const histogram = {
    red: new Array(256).fill(0),
    green: new Array(256).fill(0),
    blue: new Array(256).fill(0),
  };

  for (let i = 0; i < data.length; i += 4) {
    histogram.red[data[i]]++;
    histogram.green[data[i + 1]]++;
    histogram.blue[data[i + 2]]++;
  }

  /* Get cumulative distribution functions */
  const cumulativeRgb = {
    red: new Array(256).fill(0),
    green: new Array(256).fill(0),
    blue: new Array(256).fill(0),
  };
  /* Get cdfmin's */
  let cdfMinRgb = { red: 0, green: 0, blue: 0 };
  let sumRgb = { red: 0, green: 0, blue: 0 };

  for (let i = 0; i < 256; i++) {
    if (histogram.red[i] !== 0 && cdfMinRgb.red === 0) {
      cdfMinRgb.red = histogram.red[i];
    }
    sumRgb.red += histogram.red[i];
    cumulativeRgb.red[i] = sumRgb.red;

    if (histogram.green[i] !== 0 && cdfMinRgb.green === 0) {
      cdfMinRgb.green = histogram.green[i];
    }
    sumRgb.green += histogram.green[i];
    cumulativeRgb.green[i] = sumRgb.green;

    if (histogram.blue[i] !== 0 && cdfMinRgb.blue === 0) {
      cdfMinRgb.blue = histogram.blue[i];
    }
    sumRgb.blue += histogram.blue[i];
    cumulativeRgb.blue[i] = sumRgb.blue;
  }

  /* Get equalization && get equalized histogram */
  const equalizedRgb = {
    red: new Array(256).fill(0),
    green: new Array(256).fill(0),
    blue: new Array(256).fill(0),
  };

  for (let i = 0; i < 256; i++) {
    const hRred = Math.round(
      ((cumulativeRgb.red[i] - cdfMinRgb.red) * 255) /
        (width * height - cdfMinRgb.red)
    );
    equalizedRgb.red[i] = hRred;

    const hGreen = Math.round(
      ((cumulativeRgb.green[i] - cdfMinRgb.green) * 255) /
        (width * height - cdfMinRgb.green)
    );
    equalizedRgb.green[i] = hGreen;

    const hBlue = Math.round(
      ((cumulativeRgb.blue[i] - cdfMinRgb.blue) * 255) /
        (width * height - cdfMinRgb.blue)
    );
    equalizedRgb.blue[i] = hBlue;
  }

  for (let i = 0; i < data.length; i += 4) {
    data[i] = equalizedRgb.red[data[i]];
    data[i + 1] = equalizedRgb.green[data[i + 1]];
    data[i + 2] = equalizedRgb.blue[data[i + 2]];
  }

  return imageData;
}
